* {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
.container {
  width: 89%;
  margin-left: auto;
  margin-right: auto;
}
.my-blog-title {
  text-decoration: none;
  color: white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.my-blog-title:hover {
  color: #b1b1b1;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.login-button {
  margin-left: auto;
  text-decoration: none;
  color: white;
}

.logout-button {
  margin-left: auto;
  text-decoration: none;
  color: white;
}
.header-image {
    width: 100%;
    height: 100%;
}

.details {
    display: grid;
    grid-gap: 5%;
    width: 85%;
    margin: 0 auto;
    grid-template-columns: 20% 20% 20% 20%;
    font-size: 16px;
    letter-spacing: 0.9px;
}

.details i {
    font-size: 35px;
    color: #38d138;
}

.footer {
    padding: 30px;
    text-align: center;
    background-color: #e1e1e1;
}

.footer i {
    margin: 15px 25px;
    font-size: 25px;
}

.footer i:hover {
    cursor: pointer;
}

@media screen and (max-width: 900px) {
    .details {
        grid-template-columns: 28.33% 28.33% 28.33%;
    }
}

@media screen and (max-width: 700px) {
    .details {
        grid-template-columns: 45% 45%;
    }
}

@media screen and (max-width: 450px) {
    .details {
        grid-template-columns: 100%;
    }
    .box {
        margin-top: 15px;
    }
}

.top-button {
    position: fixed;
    bottom: 4%;
    right: 4%;
}

.top-button a {
    text-align: center;
    color: white;
}
.add-blog {
  display: inline-block;
  margin-left: 10px;
}

.create-post{
  text-decoration: none;
  color: unset;
}

.date-form {
  display: inline;
}

.date-form div {
  display: inline-block;
}

.form-filter-container .search-field {
  margin-top: 9px;
}

.start-date, .end-date {
  margin-left: 15px;
}

.form-filter-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 10px;
}

@media screen and (max-width: 400px) {
  .form-filter-container {
    flex-direction: column;
    flex: 1 1;
  }
}

@media screen and (max-width: 756px) {
  .add-blog {
    margin-top: 10px;
    margin-left: 0px;
  }
}

@media screen and (max-width: 655px) {
  .date-form div.end-date {
    display: block;
    margin-left: 0;
    margin-top: 10px;
  }
}

@media screen and (max-width: 445px) {
  .date-form div.start-date {
    display: block;
    margin-top: 10px;
    margin-left: 0;
  }
  .search-field {
    width: 88%;
  }
}
.items-grid {
    width: 98%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-around;
}
.modal {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.items-grid-item {
  margin-top: 15px;
  width: 215px;
  flex-shrink: 0;
  align-self: flex-start;
}

.modal-content {
  background-color: #dfffe8;
  margin: 20% auto;
  padding: 20px;
  width: 50%;
  font-size: 18px;
  border-radius: 5px;
  letter-spacing: 1.5px;
}

.blog-wrapper {
  padding: 10px;
  background-color: #f6f6f6;
}

.blog-title {
  text-align: center;
  font-size: 15px;
  padding: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 4px;
}

.blog-title:hover {
  cursor: pointer;
  background-color: #bfbfbf;
}

.blog-title span{
  letter-spacing: 1px;
  padding-left: 8px;
}

.blog-description {
  font-size: 14px;
  width: 195px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.blog-datetime {
  font-size: 13px;
  color: #353535;
}

.blog-datetime span {
  font-size: 12px;
}

.blog-buttons {
  display: flex;
  margin-top: 10px;
  align-items: center;
  justify-content: space-around;
}

.blog-delete-button .delete-blog-icon {
  font-size: 18px;
  padding-left: 5px;
  padding-bottom: 2px;
}
body .snackbar-error-message {
    position: fixed;
    width: 50%;
    bottom: 2%;
    left: 2%;
}

.description {
  margin-top: 20px;
}

.update-blog {
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
}

.login-form {
  width: 55%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.login-form form {
  width: 100%;
}

.form-login-button {
  margin-top: 30px;
}

.third-party-login, .new-user {
  margin-top: 15px;
}

.new-user a {
  text-decoration: none;
}

.third-party-login img {
  width: 11%;
  padding: 0 10px 0 0;
}

.snack-bar {
  background-color: green;
  width: 500px;
}

.register-form {
  width: 55%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.register-form form {
  width: 100%;
}

.register-new-user {
  margin-top: 15px;
}

.third-party-login a {
  text-decoration: none;
}

.third-party-login img {
  height: 20px;
}

.register-form-login-button {
  margin-top: 15px;
}

.register-form-login-button a{
  text-decoration: none;
}
.shared-blog {
  margin-top: 40px;
  width: 75%;
  margin-left: auto;
  margin-right: auto;
}

.shared-description {
  margin-top: 30px;
  letter-spacing: 0.8px;
}

.shared-description aside {
  line-height: 1.9;
}
.main-error-container {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100vh;
}

.error-container {
    text-align: center;
}

.error-container p span {
    font-size: 40px;
    padding-right: 10px;
    text-shadow: 0 15px 1px rgba(0, 0, 0, 0.3);
}
